@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
header {
    height: 100vh;
    padding-top: 30vh;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* =============Header=Socials========== */

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 2rem;
    bottom: 3rem;
}

.header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

@media screen and (max-width: 1024px) {
    .header__container h1 {
        font-size: 2.5rem;
    }
}
nav {
    background:     rgba(0, 0, 0,0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    border-radius: 1rem;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}

nav a{
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1rem;
}
nav a:hover{
    background:     rgba(0, 0, 0,0.5);
}

nav a.active{
    background: var(--color-bg);
    color: var(--color-white);
}
.portfolio__container{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem;
}
.cen{
    width:100%;
    display: flex;
    justify-content: center;
}

.portfolio__item{
    background: var(--color-bg-variant);
    padding: 1.3rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.portfolio__item:hover{
    border-color: var(--color-primary-variant);
    background: transparent;
}

.portfolio__item-image{
    border-radius: 1.5rem;
    overflow: hidden;
    width: auto;
    height: 15rem;
}

.portfolio__item h3{
    margin: 1.2 0 2rem;
}

/* ===============Med=Devices=============== */
@media screen and (max-width: 1024px) {
.portfolio__container{
    grid-template-columns: 1fr 1fr;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}
}

/* ===============Small=Devices=============== */
@media screen and (max-width: 600px) {
    .portfolio__container{
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
}
.skills__container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
    

}

.skills__container > div{
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 1rem;
    border: 1px solid transparent;
}

.skills__container > div h3{
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}

.skills__content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 2rem;
    row-gap: 2rem;
}

.skills__details{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}

.skills__details-icon{
    margin-top: 6px;
    color: var(--color-primary);
}

/* ===============Med=Devices=============== */
@media screen and (max-width: 1024px) {
    .skills__container{
        grid-template-columns: 1fr;
    }
    .skills__container > div{
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }
    .skills__content{
        padding: 1rem;
    }
}

/* ===============Small=Devices=============== */
@media screen and (max-width: 600px) {
    .skills__container{
        grid-gap: 1rem;
        gap: 1rem;
    }
    .skills__container > div{
        width: 100%;
        padding: 2rem 1rem;
    }
}
p{
    padding-left: 15rem;
    padding-right: 14rem;
}

/* ===============Med=Devices=============== */
@media screen and (max-width: 1024px) {
    p{
        padding-left: 3rem;
        padding-right: 3rem;
    }
    }
    
    /* ===============Small=Devices=============== */
    @media screen and (max-width: 600px) {
        p{
            padding-left: 1.5rem;
            padding-right: 1.5rem;
        }
    }
footer {
    background: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}

footer a {
    color: var(--color-bg);
}

.footer__logo {
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}

.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footer__socials {
    display: flex;
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 4rem;
}

.footer__socials a {
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}

.footer__socials a:hover {
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}

.footer__copyright {
    margin-bottom: 4rem;
    color: var(--color-bg);
}

/* ===============Small=Devices=============== */
@media screen and (max-width: 600px) {
    .permalinks{
        flex-direction: column;
        grid-gap: 1.5rem;
        gap: 1.5rem;
    }

    .footer__socials{
        margin-bottom: 2.6rem;
    }
}
* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: #1f1f38;
    --color-bg-variant: #2c2c6c;
    --color-primary: #4db5ff;
    --color-primary-variant: rgba(77, 181, 255, 0.4);
    --color-white: #fff;
    --color-light: rgba(255, 255, 255, 0.6);


    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
    --transition: all 400ms ease;
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: "Poppins", sans-serif;
    background: #1f1f38;
    background: var(--color-bg);
    color: #fff;
    color: var(--color-white);
    line-height: 1.7;
}


/* styles */

.container {
    width: 75%;
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1,h2,h3,h4.h5{
    font-weight: 500;
}
h1{
    font-size: 4.5rem;
}
section{
    height: 100vh auto;
    margin-top: 8rem;
}

section > h2, section > h5{
    text-align: center;
    color: rgba(255, 255, 255, 0.6);
    color: var(--color-light);
} 

section > h2 {
    color: #4db5ff;
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.textlight{
    color: rgba(255, 255, 255, 0.6);
    color: var(--color-light);
}

a{
    color: #4db5ff;
    color: var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

a:hover{
    color: #fff;
    color: var(--color-white);
}

.btn{
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #4db5ff;
    color: var(--color-primary);
    padding: 0.7rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid #4db5ff;
    border: 1px solid var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

.btn:hover{
    background: #fff;
    background: var(--color-white);
    color: #1f1f38;
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary{
    background: #4db5ff;
    background: var(--color-primary);
    color: #1f1f38;
    color: var(--color-bg);
}

img{
    display: block;
    width: 100%;
    object-fit: cover;
}

/* =============-Tablets-med-================== */
@media screen and (max-width: 1024px) {
    .container{
        width: 86%;
        width: var(--container-width-md);
    }
    section{
        margin-top: 6rem;
    }
}

/* =============-phones-small-================== */
@media screen and (max-width: 600px) {
    .container{
        width: 90%;
        width: var(--container-width-sm);
    }
    section{
        margin-top: 2rem;
    }
}
